.dropdown-container {  
    position: relative;
}

.dropdown {
  display: flex;
  gap: 8px;
  justify-content: space-between;
  align-items: center;
  padding: 0 16px;
  background: linear-gradient(0deg, #fafafc 0%, #fff 100%);
 border: 1px solid rgba(178, 187, 213, 0.5);
  border-radius: 4px;
  cursor: pointer;
  height: 40px; 
  width: 226px;
}

.dropdown-options {
  width: 100%;
  position: absolute;
  z-index: 1;
  top: 38px;
  background: linear-gradient(0deg, #fafafc 0%, #fff 100%);
  padding: 0;
  margin: 0;
  border: 1px solid rgba(178, 187, 213, 0.5);
  border-top: none;
}

.dropdown-option:hover {
  background: #f5f6fa;
  padding-left: 16px;
}

.dropdown-option {
  padding: 12px 0;
  padding-left: 16px;
}

.dropdown-container ul {
  list-style-type: none;
}

.dropdown img {
  width: 8px;
}
